import React from 'react';

const Profile = props => {
  const logout = () => {
    localStorage.removeItem('user');
    props.history.push('login');
    props.setUser({ user: null });
  };

  return (
    <div style={{
      width: 400,
      margin: 'auto',
      paddingTop: '20px',
      textAlign: 'center',
    }}>
      <h1>Here is my profile</h1>
      <button onClick={logout}>Logout</button>
    </div>
  );
};
export default Profile;
