import React, { Component } from 'react';
import { Row, FormGroup, FormControl, Button } from 'react-bootstrap';
import './login.sass';
import {
  isEmail,
  isEmpty,
  isLength,
  isContainWhiteSpace,
} from '../../shared/validator';

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formData: {}, // Contains login form data
      errors: {}, // Contains login field errors
      formSubmitted: false, // Indicates submit status of login form
      loading: false, // Indicates in progress state of login form
    };
    this.credentials = {
      username: 'admin',
      password: '12345',
    };
  }

  handleInputChange = event => {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    let { formData } = this.state;
    formData[name] = value;

    this.setState({
      formData: formData,
    });
  };

  validateLoginForm = e => {
    let errors = {};
    const { formData } = this.state;

    if (isEmpty(formData.email)) {
      errors.email = "Email can't be blank";
    } else if (!isEmail(formData.email)) {
      errors.email = 'Please enter a valid email';
    }

    if (isEmpty(formData.password)) {
      errors.password = "Password can't be blank";
    } else if (isContainWhiteSpace(formData.password)) {
      errors.password = 'Password should not contain white spaces';
    } else if (!isLength(formData.password, { gte: 6, lte: 16, trim: true })) {
      errors.password = "Password's length must between 6 to 16";
    }

    if (isEmpty(errors)) {
      return true;
    } else {
      return errors;
    }
  };

  login = e => {
    console.log('this.props', this.props);
    console.log('this.state', this.state);
    e.preventDefault();

    const { formData } = this.state;
    let errors = this.validateLoginForm();
    if (
      this.credentials.username === formData.email &&
      this.credentials.password === formData.password
    ) {
      localStorage.setItem('user', 'true');
      this.props.setUser({ user: true });
      return this.props.history.push('/profile');
    }
    if (errors === true) {
      alert('You are successfully signed in...');
      window.location.reload();
    } else {
      this.setState({
        errors: errors,
        formSubmitted: true,
      });
    }
  };

  render() {
    const { errors, formSubmitted } = this.state;

    return (
      <div className="Login">
        <Row>
          <form onSubmit={this.login}>
            <FormGroup
              controlId="email"
              validationState={
                formSubmitted ? (errors.email ? 'error' : 'success') : null
              }
            >
              <div>Email</div>
              <FormControl
                type="text"
                name="email"
                placeholder="Enter your email"
                onChange={this.handleInputChange}
              />
              {errors.email && <div>{errors.email}</div>}
            </FormGroup>
            <FormGroup
              controlId="password"
              validationState={
                formSubmitted ? (errors.password ? 'error' : 'success') : null
              }
            >
              <div>Password</div>
              <FormControl
                type="password"
                name="password"
                placeholder="Enter your password"
                onChange={this.handleInputChange}
              />
              {errors.password && <div>{errors.password}</div>}
            </FormGroup>
            <Button type="submit" bsStyle="primary">
              Sign-In
            </Button>
          </form>
        </Row>
      </div>
    );
  }
}

export default Login;
